.contactForm{
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 30rem;
    
}
.contactForm>*{
    font-family: monospace;
    margin: 1rem;
}

.contactForm button{
    width: 50%;
}
