.pageComponent{
    position: relative;

    display: block;
    width: 100vw;
    min-height: 100vh;
    border-bottom: 2px dotted gray;
}
.primary-btn{
    /* text-transform: uppercase; */
    padding: 1rem;
    font-size: 1.2rem;
    color: white;
    background-color: rgb(34, 161, 235);
    border: none;
}
.primary-btn:hover{
    cursor:pointer;
    opacity: 0.8;
}

#container::before{
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(0deg,#24292eae,#24292eae);
    /* z-index: 0; */
  
  }