.NavBar{
    width: 100%;
    height: 6vh;
    position: relative;
    /* background-color: #5B7DB1; */
    /* rgba(67, 126, 235, 0.801); */
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: rgb(218, 214, 214);
    font-size: 1.5rem;
    animation: SlideIn 0.3s ease-in-out;
    /* filter: blur(4px); */
}
@keyframes SlideIn{
    0% {
        transform: translateY(-100%);
    }
    100% {
        transform: translateY(0);
    }
}
