.NavBar__item{
    flex: 1;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0,0,0,0);
    z-index: 2;
}

.NavBar__item:hover{
    cursor: pointer;
}

.NavBar__item:hover::after{
    content: "";
    position: absolute;
    height: 70%;
    /* width: 0%; */
    border-bottom: 2px rgb(255, 255, 255) solid;
    animation: Underline 0.1s linear forwards;
}

@keyframes Underline {
    from{
        width: 0%;
    }
    to{
        width: 15%;
    }
}