.card{
    width: 15rem;
    height: 22rem;
    background: linear-gradient(135deg, #f7e2e227,#5b7db198);
    color:#F7E2E2;
    /* background: linear-gradient(135deg, rgba(248, 247, 247, 0.3),rgba(67, 126, 235, 0.801)); */
    border: 0px;
    border-radius: 1rem;
    box-shadow: 5px 5px rgba(34,34,34,0.5);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    /* padding: 5%; */
    animation: SlideIn 1s linear;
}
@keyframes SlideIn{
    0% {
        transform: translateY(-100%);
    }
    40% {
        transform: translateY(0);
    }
    75% {
        transform: translateY(-10%);
    }
    100% {
        transform: translateY(0);
    }
}
@keyframes Appear{
    from{
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.card img{
    width: 50%;
    height: auto;
    border: 0px;
    border-radius: 50%;
}

.contactLink{
    width: 100%;
    height:20%;
    display:flex;
    justify-content: space-around;
    /* animation: Appear 1s ease-in-out; */

}

.buttonLink{
    cursor:pointer;
    flex: 1;
    /* margin: 5px; */
    height: 100%;
    background-size: 3rem 3rem;
    background-position: center;
    background-repeat: no-repeat;
    /* padding: 20px; */
}
.buttonLink:hover{
    background-size: 3.5rem 3.5rem;
}
.buttonLink+.buttonLink{
    border-left: 1px dotted black;
}

#socialOne{
    background-image: url("../../public/assets/facebook.png");
}


#socialTwo{
    background-image: url("../../public/assets/github.png")
}
#socialThree{
    background-image: url("../../public/assets/twitter.png")
}