.Landing{
    position: absolute;
    top: 40%;
    right: 15%;
    /* height:  */
    transform: translate(0%,-50%);
    opacity: 0;
    /* height: 50rem; */

    color: #fff;
    width: 50vw;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    text-align: end;
    animation: Appear 3s linear forwards;
    animation-delay: 1s;
}

@keyframes Appear{
    from{
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.primary{
    font-size: 5rem;
    /* line-height: 1rem; */
}
.secondary{
    font-size: 2rem;
}
.Landing p {
    /* line-height: 0.2rem; */
}