.About{
    margin: 10px 0;
    width: min(95vw, 60rem);
    min-height: 60rem;
    background-color: rgb(218, 214, 214);
    padding: 30px 5%;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    animation: SlideIn 0.3s ease-in-out;
    box-sizing: border-box;
    text-align: justify;
}
.About ul{
    padding-left: 20px;
}
.About li + li{
    margin-top: 10px;
}
.Name{
    font-size: 3.3rem;
    line-height: 1rem;
}

.PersonalTitle{
    font-weight: normal;
}

.Contact{
    display: grid;
    grid-template-columns: 40% 60%;
    /* grid-template-rows: auto; */
}
.Contact div{
    height: 1.5rem;
}

.Education__school{
    display:grid;
    grid-template-columns: 25% 75%;
}
.Education__time{
    font-style: italic;
}

.Education b{
    font-size: 1.2rem;
    /* line-height: 0.8rem; */
}

.Education b+p{
    line-height: 0.5rem;
}