.Blog{
    margin: 10px 0;
    width: min(95vw, 60rem);
    /* min-height: 60rem; */
    /* calc(94vh - 20px); */
    background-color: rgb(218, 214, 214);
    padding: 20px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    animation: SlideIn 0.3s ease-in-out;
    box-sizing: border-box;
    /* font-family:  Neue, Segoe UI, Helvetica, Arial, Lucida Grande, sans-serif; */
    
    
    /* font-size: 1.1rem; */
}

.Blog a{
    text-decoration: none;
}
.Blog a:hover{
    text-decoration: underline;
}
.Blog p{
    line-height: 1.7rem;
    text-align: justify;
}
.Blog a{
    word-break: break-all;
}

@keyframes SlideIn{
    0% {
        transform: translate(-50%,100%);
    }
    100% {
        transform: translate(-50%,0);
    }
}

.mainPost{
    /* position: absolute; */
    /* padding: 10px; */
    /* height: 30rem; */
    width: 100%;
    border-bottom: 1px dotted gray;
    padding: 30px 10%;
    box-sizing: border-box;
}

.mainPost h1{
    cursor: pointer;
}

.mainPost img{
    height: 30vh;
    width: 100%;
    object-fit: cover;
}

.secondaryPosts{
    display: flex;
    /* grid-template-columns: auto auto auto; */
    flex-wrap: wrap;
    width: 100%;
    /* align-self:center; */
    /* transform: translateX(-2.2%); */
    height: 20rem;
    flex: 1;
    /* flex-direction:column; */
    padding-bottom: 10px;
    justify-content: space-around;
    /* position: absolute; */

}

.postPreview{
    /* flex: 1; */
    cursor:pointer;
    /* width: 31%; */
    flex: 1 1 31%;
    height: 25rem;
    padding-right:10px;
    padding-top: 10px;
    /* border: 1px dotted gray; */
    justify-content: center;
    
}
.postPreview p{
    text-align: left;
}


.postPreview + .postPreview{
    /* border-left: 1px dotted gray; */
    /* border-right: 1px dotted gray; */
    padding-left: 10px;
}

.postPreview img{
    width: 100%;
    height: 8rem;
    align-self: center;
    object-fit: cover;
}


.postPreview:hover{
    padding: 10px;
    background-color: rgb(218, 214, 214);;
    transform: scale(1.1);
    border: 1px solid black;
}